import { Fragment, useState, useEffect, useContext } from "react"
import { Link } from "react-router-dom"
import {
  Row,
  Col,
  Card,
  Label,
  Input,
  Table,
  Modal,
  Button,
  CardBody,
  ModalBody,
  ModalHeader,
  FormFeedback,
  UncontrolledTooltip
} from "reactstrap"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import {
  storeName,
  pageTitle,
  detailFromGrid,
  setDataById,
  postData,
  putData
} from "./store"
import { getDataList as getDataListPermission } from "../../apps/permission/store"
import { getDataList as getDataListRolePermission } from "../../apps/role_permission/store"
import { Copy, Info } from "react-feather"
import { useForm, Controller } from "react-hook-form"
import AvatarGroup from "@components/avatar-group"
import { useDispatch, useSelector } from "react-redux"
import illustration from "@src/assets/images/illustration/faq-illustrations.svg"
import toast from "react-hot-toast"
import { useTranslation } from "react-i18next"
import { Loader } from "@src/shared/CommonComponents"
import UILoader from "@components/ui-loader"

const RoleCards = () => {
  // ** States
  const { t } = useTranslation()
  const [showModal, setShowModal] = useState(false)
  const [modalType, setModalType] = useState(`${t("Add New")}`)
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])
  const storePermission = useSelector((state) => state.apps_permission)
  const [permissionDropdown, setPermissionDropdown] = useState([])
  const [permissionDropdownOld, setPermissionDropdownOld] = useState([])
  const [description, setDescription] = useState("")
  const [formState, setFormState] = useState(null)
  const [loading, setLoading] = useState(false)
  const [loadingText, setLoadingText] = useState("Please Wait")
  const { colors } = useContext(ThemeColors)

  const {
    reset,
    control,
    setError,
    setValue,
    handleSubmit,
    formState: { errors }
  } = useForm({})

  const handleEdit = (rowData) => {
    // e.preventDefault()
    setDescription(rowData.title)
    console.log(rowData)
    setFormState("edit")
    setModalType(`${t("Edit")}`)
    if (!detailFromGrid) {
      setLoadingText(
        `${t("Loading")} ${t("Detail")} ${t("Data")} ${t(pageTitle)}`
      )
      setLoading(true)
      dispatch(setDataById(rowData)).then((x) => {
        setLoading(false)
        setShowModal(true)
      })
      dispatch(
        getDataListRolePermission({
          filter_id_role: rowData.id
        })
      ).then((res) => {
        if (res.meta.requestStatus === "rejected") {
          setLoading(false)
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        } else {
          // console.log(storePermission.dataDropdown)
          // console.log(res.payload.data)
          // const fillPermission = [
          //   ...res.payload.data,
          //   ...storePermission.dataDropdown
          // ]
          let responseData = [...res.payload.data]
          let arr3 = storePermission.dataDropdown.map((item, i) =>
            Object.assign({}, item, responseData[i])
          )
          let arr4 = storePermission.dataDropdown.map((item, i) =>
            Object.assign({}, item, responseData[i])
          )
          // console.log(arr3)

          setPermissionDropdown(arr3)
          setPermissionDropdownOld(arr4)
        }
      })
    } else {
      setLoading(true)
      setLoadingText(
        `${t("Loading")} ${t("Detail")} ${t("Data")} ${t(pageTitle)}`
      )
      dispatch(setDataById(rowData)).then((x) => {
        setLoading(false)
        setShowModal(true)
      })
    }
  }

  const handleChangeChk = (chkValue, index, field) => {
    // console.log(chkValue.target.checked)
    // console.log(field)
    let permissionDropdownCopy = Object.assign([], permissionDropdown)
    // console.log(permissionDropdownCopy)
    permissionDropdownCopy[index][field] = chkValue.target.checked
    setPermissionDropdown(permissionDropdownCopy)
  }

  const handleAdd = async () => {
    setFormState("add")
    setModalType(`${t("Add New")}`)
    if (storePermission.data.length === 0 && !storePermission.isLoading) {
      setLoading(true)
      await dispatch(getDataListPermission({})).then((res) => {
        let responseData = [...res.payload.data]
        let arr3 = responseData
        let arr4 = responseData
        setPermissionDropdown(arr3)
        setPermissionDropdownOld(arr4)

        setShowModal(true)
        setLoading(false)
      })
    } else {
      let arr3 = [...storePermission.dataDropdown]
      let arr4 = [...storePermission.dataDropdown]
      setPermissionDropdown(arr3)
      setPermissionDropdownOld(arr4)

      setShowModal(true)
      setLoading(false)
    }
  }

  const onSubmit = (data) => {
    if (data.description.length) {
      // setShowModal(false)
      formState === "edit" ? updateDataToApi(data) : postDataToApi(data)
    } else {
      setError("description", {
        type: "manual"
      })
    }
  }

  const postDataToApi = (data) => {
    if (
      Object.values(data).every((field) => {
        return field !== null && field !== ""
      })
    ) {
      setLoadingText(`${t(`Creating a New ${pageTitle}`)}`)
      const isSameData = (a, b) =>
        a.id_permission === b.id_permission &&
        a.post === b.post &&
        a.put === b.put &&
        a.delete === b.delete &&
        a.get === b.get

      const onlyInLeft = (left, right, compareFunction) =>
        left.filter(
          (leftValue) =>
            !right.some((rightValue) => compareFunction(leftValue, rightValue))
        )
      const onlyInA = onlyInLeft(
        permissionDropdown,
        permissionDropdownOld,
        isSameData
      )
      if (onlyInA.length > 0) {
        data["role_permission"] = onlyInA
      }
      dispatch(postData(data)).then((res) => {
        // console.log(res)
        if (res.meta.requestStatus === "fulfilled") {
          setShowModal(false)
          toast.success(`${t("Successfully Created")} ${t(pageTitle)}`, {
            style: {
              padding: "16px",
              color: colors.success.main,
              border: `1px solid ${colors.success.main}`
            },
            iconTheme: {
              primary: colors.success.main
            }
          })
        } else if (res.meta.requestStatus === "rejected") {
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        }
      })
    } else {
      for (const key in data) {
        if (data[key] !== null && data[key]?.length === 0) {
          setError(key, {
            type: "manual"
          })
        }
      }
    }
  }

  function sortedObject(unordered) {
    return Object.keys(unordered)
      .sort()
      .reduce((obj, key) => {
        obj[key] = unordered[key]
        return obj
      }, {})
  }

  const countChanges = (data) => {
    if (formState === "edit") {
      // console.log(JSON.stringify(permissionDropdownOld))
      // console.log(JSON.stringify(permissionDropdown))
      const permissionState =
        JSON.stringify(permissionDropdownOld) !==
        JSON.stringify(permissionDropdown)
      return store.selectedData.title !== data.description || permissionState
    } else {
      return true
    }
  }

  const updateDataToApi = (data) => {
    if (countChanges(data)) {
      setLoadingText(`${t("Updating")} ${t(pageTitle)} ${t("Detail")} Data`)
      const isSameData = (a, b) =>
        a.id === b.id &&
        a.id_permission === b.id_permission &&
        a.post === b.post &&
        a.put === b.put &&
        a.delete === b.delete &&
        a.get === b.get

      const onlyInLeft = (left, right, compareFunction) =>
        left.filter(
          (leftValue) =>
            !right.some((rightValue) => compareFunction(leftValue, rightValue))
        )
      const onlyInA = onlyInLeft(
        permissionDropdown,
        permissionDropdownOld,
        isSameData
      )
      if (onlyInA.length > 0) {
        data["role_permission"] = onlyInA
      }
      // console.log(data)
      dispatch(putData(data)).then((res) => {
        if (res.meta.requestStatus === "fulfilled") {
          setShowModal(false)
          toast.success(`${t("Successfully Updated")} ${t(pageTitle)}`, {
            style: {
              padding: "16px",
              color: colors.success.main,
              border: `1px solid ${colors.success.main}`
            },
            iconTheme: {
              primary: colors.success.main
            }
          })
        } else if (res.meta.requestStatus === "rejected") {
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        }
      })
    } else {
      toast(`${t("You haven't changed anything")}`, { icon: "" })
    }
  }

  const onReset = () => {
    setShowModal(false)
    reset({ roleName: "" })
  }

  const handleModalClosed = () => {
    setModalType(`${t("Add New")}`)
    setValue("roleName")
  }

  useEffect(() => {
    if (
      storePermission.dataDropdown.length === 0 &&
      !storePermission.isLoading
    ) {
      dispatch(getDataListPermission({ grid_mode: true }))
    }
  }, [dispatch, storePermission.dataDropdown.length])

  return (
    <Fragment>
      <UILoader blocking={store.isLoadingSummary} loader={<Loader />}>
        <Row>
          {store.dataSummary.map((item, index) => {
            return (
              <Col key={index} xl={4} md={6}>
                <Card>
                  <CardBody>
                    <div className="d-flex justify-content-between">
                      <span>{`Total ${item.totalUsers} users`}</span>
                      {item?.users?.length > 0 ? (
                        <AvatarGroup
                          data={item.users.map((x) => ({
                            size: x.size,
                            title: x.title,
                            id: x.id,
                            img:
                              x?.img !== null
                                ? x.img
                                : require(`@src/assets/images/avatars/avatar-blank.png`)
                                    .default
                          }))}
                        />
                      ) : (
                        <div style={{ height: "30px" }} className=""></div>
                      )}
                    </div>
                    <div className="d-flex justify-content-between align-items-end mt-1 pt-25">
                      <div className="role-heading  text-truncate">
                        <h4 className="fw-bolder mb-0">{item.title ?? "-"}</h4>
                        <div>
                          <small className="fw-bolder">
                            {item.name ?? "-"}
                          </small>
                        </div>
                        <Link
                          to="/"
                          className="role-edit-modal"
                          onClick={(e) => {
                            e.preventDefault()
                            handleEdit(item)
                          }}
                        >
                          <small className="fw-bolder">
                            {t("Edit")} {t("Role")}
                          </small>
                        </Link>
                      </div>
                      <Link
                        to=""
                        className="text-body"
                        onClick={(e) => e.preventDefault()}
                      >
                        {/* <Copy className="font-medium-5" /> */}
                      </Link>
                    </div>
                  </CardBody>
                </Card>
              </Col>
            )
          })}
          {location.host === "admin.insaba.co.id" && (
            <Col xl={4} md={6}>
              <Card>
                <Row className="mb-2">
                  <Col sm={5}>
                    <div className="d-flex align-items-end justify-content-center h-100">
                      <img
                        className="img-fluid mt-2"
                        src={illustration}
                        alt="Image"
                        width={85}
                      />
                    </div>
                  </Col>
                  (
                  <Col sm={7}>
                    <CardBody className="text-sm-end text-center ps-sm-0">
                      <Button
                        color="primary"
                        className="text-nowrap mb-1"
                        onClick={() => {
                          handleAdd()
                        }}
                      >
                        {t("Add New Role")}{" "}
                      </Button>
                      <p className="mb-0">
                        {t("Add a new role, if it does not exist")}
                      </p>
                    </CardBody>
                  </Col>
                  )
                </Row>
              </Card>
            </Col>
          )}
        </Row>

        {showModal && (
          <Modal
            isOpen={showModal}
            onClosed={handleModalClosed}
            toggle={() => setShowModal(!showModal)}
            className="modal-dialog-centered modal-xl"
          >
            {/* <UILoader
              blocking={store.isLoadingAddEdit || store.isLoadingDetail}
              loader={<Loader />}
            > */}
            <ModalHeader
              className="bg-transparent"
              toggle={() => setShowModal(!showModal)}
            ></ModalHeader>
            {!store.loading && (
              <ModalBody className="px-5 pb-5">
                <div className="text-center mb-4">
                  <h1>
                    {modalType} {t("Role")}
                  </h1>
                  <p>
                    {formState === "edit"
                      ? `${t("Set role name and permissions")}`
                      : t("Create new role")}
                  </p>
                </div>
                <Row tag="form" onSubmit={handleSubmit(onSubmit)}>
                  <Col xs={12}>
                    <Label className="form-label" for="description">
                      {t("Role Name")}
                    </Label>
                    <Controller
                      name="description"
                      control={control}
                      defaultValue={description}
                      render={({ field }) => (
                        <Input
                          {...field}
                          id="description"
                          placeholder={t("Enter role name")}
                          invalid={errors.description && true}
                        />
                      )}
                    />
                    {errors.description && (
                      <FormFeedback>
                        {t("Please enter a valid role name")}
                      </FormFeedback>
                    )}
                  </Col>
                  <Col xs={12}>
                    <h4 className="mt-2 pt-50">{t("Role Permissions")}</h4>
                    <Table className="table-flush-spacing" responsive>
                      <tbody>
                        {/* <tr>
                      <td className="text-nowrap fw-bolder">
                        <span className="me-50">
                          {" "}
                          {t("Administrator Access")}
                        </span>
                        <Info size={14} id="info-tooltip" />
                        <UncontrolledTooltip
                          placement="top"
                          target="info-tooltip"
                        >
                          {t("Allows a full access to the system")}
                        </UncontrolledTooltip>
                      </td>
                      <td>
                        <div className="form-check">
                          <Input type="checkbox" id="select-all" />
                          <Label className="form-check-label" for="select-all">
                            {t("Select All")}
                          </Label>
                        </div>
                      </td>
                    </tr> */}
                        {permissionDropdown.map((role, index) => {
                          return (
                            <tr key={index}>
                              <td className="text-nowrap fw-bolder">
                                <span className="me-50"> {role.label}</span>
                                <Info size={14} id={`info_${role.id}`} />
                                <UncontrolledTooltip
                                  placement="top"
                                  target={`info_${role.id}`}
                                >
                                  {t(role.description)}
                                </UncontrolledTooltip>
                              </td>
                              <td>
                                <div className="d-flex">
                                  <div className="form-check me-3 me-lg-5">
                                    <Input
                                      checked={role.get}
                                      type="checkbox"
                                      id={`read-${role.id}`}
                                      onChange={(event) => {
                                        handleChangeChk(event, index, "get")
                                      }}
                                    />
                                    <Label
                                      className="form-check-label"
                                      for={`read-${role.id}`}
                                    >
                                      {t("Read")}
                                    </Label>
                                  </div>
                                  <div className="form-check me-3 me-lg-5">
                                    <Input
                                      checked={role.post}
                                      type="checkbox"
                                      id={`write-${role.id}`}
                                      onChange={(event) => {
                                        handleChangeChk(event, index, "post")
                                      }}
                                    />
                                    <Label
                                      className="form-check-label"
                                      for={`write-${role.id}`}
                                    >
                                      {t("Create")}
                                    </Label>
                                  </div>
                                  <div className="form-check me-3 me-lg-5">
                                    <Input
                                      checked={role.put}
                                      type="checkbox"
                                      id={`create-${role.id}`}
                                      onChange={(event) => {
                                        handleChangeChk(event, index, "put")
                                      }}
                                    />
                                    <Label
                                      className="form-check-label"
                                      for={`create-${role.id}`}
                                    >
                                      {t("Update")}
                                    </Label>
                                  </div>
                                  <div className="form-check">
                                    <Input
                                      checked={role.delete}
                                      type="checkbox"
                                      id={`delete-${role.id}`}
                                      onChange={(event) => {
                                        handleChangeChk(event, index, "delete")
                                      }}
                                    />
                                    <Label
                                      className="form-check-label"
                                      for={`delete-${role.id}`}
                                    >
                                      {t("Delete")}
                                    </Label>
                                  </div>
                                </div>
                              </td>
                            </tr>
                          )
                        })}
                      </tbody>
                    </Table>
                  </Col>
                  <Col className="text-center mt-2" xs={12}>
                    <Button type="submit" color="primary" className="me-1">
                      {t("Submit")}
                    </Button>
                    <Button type="reset" outline onClick={onReset}>
                      {t("Cancel")}
                    </Button>
                  </Col>
                </Row>
              </ModalBody>
            )}
            {/* </UILoader> */}
          </Modal>
        )}
      </UILoader>
    </Fragment>
  )
}

export default RoleCards
